import React from 'react';

const CalendarWeek = () => (
  <ul className="calendar-grid week">
    <li>S</li>
    <li>M</li>
    <li>T</li>
    <li>W</li>
    <li>T</li>
    <li>F</li>
    <li>S</li>
  </ul>
);

export default CalendarWeek;



